element) => {\n          //zIndex must be an integer (proptan zIndex number verilmeli)\n          let strzIndex = <any>this.zIndex - 1;\n          element.style.setProperty(\n            \"z-index\",\n            strzIndex.toString(),\n            \"important\"\n          );\n        });\n        this.element.style.setProperty(\"z-index\", this.zIndex, \"important\");\n      }\n    },\n    configureForEditor() {\n      if (\n        this._renderingProps.isEditMode ||\n        this._renderingProps.isEditMode == null ||\n        this._renderingProps.isEditMode == undefined\n      )\n        (<HTMLDivElement>this.$refs[\"draggableDiv\"])!.style.setProperty(\n          \"position\",\n          \"relative\"\n        );\n      else\n        (<HTMLDivElement>this.$refs[\"draggableDiv\"])!.style.setProperty(\n          \"position\",\n          \"absolute\"\n        );\n    },\n  },\n  watch: {\n    pinned(val) {\n      this.internalPinned = val;\n    },\n    resizable(val) {\n      this.internalResizable = val;\n    },\n    isVisible(val) {\n      if (val) {\n        this.$nextTick(() => this.doInitialMeasurements());\n      }\n    },\n  },\n  beforeDestroy() {\n    this.resizeObserver?.disconnect();\n    document.removeEventListener(\"click\", this.clickEvent);\n  },\n});\n</script>\n<style scoped>\ndiv[name=\"draggableDiv\"] {\n  border-radius: 10px;\n  position: absolute;\n  background-color: #f1f1f1;\n  text-align: center;\n  box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 12px;\n}\n\n#draggableDivHeader {\n  border-top-left-radius: 10px;\n  border-top-right-radius: 10px;\n}\n.draggableDivBody {\n  overflow: auto;\n  border-bottom-left-radius: 10px;\n  border-bottom-right-radius: 10px;\n}\n.custom-icon {\n  font-size: 20px;\n}\n.b-close {\n  position: absolute !important;\n  right: 8px;\n  top: 15px;\n  cursor: pointer;\n}\n.fullscreen {\n  position: absolute !important;\n  right: 33px;\n  top: 15px;\n  cursor: pointer;\n}\n.fullscreenExit {\n  position: absolute !important;\n  