












































































import Vue from "vue";
export default Vue.extend({
  name: "QLightBox",
  data: function () {
    return {
      element: { style: { width: "0", height: "0" } } as HTMLDivElement,
      resizeObserver: null as ResizeObserver | null,
      calculatedHeight: "",
      calculatedWidth: "",
      calculateTop: "",
      calculateLeft: "",
      limitedTop: null as string | null,
      limitedLeft: null as string | null,
      preFullScreenStyle: null as any,
      reachedMinSize: false,
      reachedMaxSize: false,
      minWidth: 70 as number,
      maxWidth: undefined as number | undefined,
      minHeight: 70 as number,
      maxHeight: undefined as number | undefined,
      internalPinned: this.pinned,
      internalResizable: this.resizable,
      pos1: 0,
      pos2: 0,
      pos3: 0,
      pos4: 0,
      //For ViewPort
      _bottom: 0,
      _left: 0,
      _right: 0,
      _top: 0,
      rect: new DOMRect(),
      resizing: false,
      resizeHandle: "",
      initialRect: null as DOMRect | null,
      coords: ["top-left", "top-right", "bottom-left", "bottom-right"],
      isFullScreen: false as Boolean,
      isMinimized: false as Boolean,
      savedPosition: null,
      currentWidth: "",
      currentHeight: "",
      insetRight: "",
    };
  },
  props: {
    width: {
      type: String,
      default: "500px",
    },
    height: {
      type: String,
      default: "400px",
    },
    headerPadding: {
      type: String,
      default: "27px",
    },
    right: {
      type: String,
      default: "auto",
    },
    left: {
      type: String,
      default: "0px",
    },
    top: {
      type: String,
      default: "auto",
    },
    bottom: {
      type: String,
      default: "0px",
    },
    zIndex: {
      type: String,
    },
    pinned: {
      type: Boolean,
    },
    backgroundColor: {
      type: String,
    },
    _renderingProps: {
      type: Object,
    },
    class: {
      type: String,
    },
    border: {
      type: Boolean,
      default: true,
    },
    closeBtn: {
      type: Boolean,
      default: true,
    },
    fullScreen: {
      type: Boolean,
      default: false,
    },
    resizable: {
      type: Boolean,
      default: false,
    },
    minimizable: {
      type: Boolean,
      default: false,
    },
  },
  mounted() {
    this.configureForEditor();
    this.dragElement(<HTMLDivElement>this.$refs["draggableDiv"]);
    const el = this.$refs["draggableDiv"] as HTMLDivElement;
    const checkVisibilityAndMeasure = () => {
      if (getComputedStyle(el).display === "none") {
        // ðŸš¨ HÃ¢lÃ¢ gÃ¶rÃ¼nmez, tekrar dene
        requestAnimationFrame(checkVisibilityAndMeasure);
        return;
      }
      // âœ… GÃ¶rÃ¼nÃ¼r, Ã¶lÃ§Ã¼mleri yap
      this.doInitialMeasurements();
    };
    requestAnimationFrame(checkVisibilityAndMeasure);
  },
  updated() {
    const el = this.$refs["draggableDiv"] as HTMLDivElement;
    this.element = el;
  },
  computed: {
    classObject: function () {
      return {
        [this.class]: true,
        minimized: (<any>this).isMinimized,
      };
    },
  },
  methods: {
    doInitialMeasurements() {
      this.$nextTick(() => {
        requestAnimationFrame(() => {
          const el = this.$refs["draggableDiv"] as HTMLDivElement;

          if (!el.style.width) {
            el.style.width = this.width;
          }
          if (!el.style.height) {
            el.style.height = this.height;
          }
          if (this.top) {
            el.style.top = this.top;
          }
          if (this.left) {
            el.style.left = this.left;
          }
          if (this.right) {
            el.style.right = this.right;
          }
          if (this.bottom) {
            el.style.bottom = this.bottom;
          }
          const computedStyle = window.getComputedStyle(el);
          parseFloat(computedStyle.top) < 0
            ? (el.style.top = "0px")
            : (el.style.top = computedStyle.top);
          parseFloat(computedStyle.bottom) < 0
            ? (el.style.bottom = "0px")
            : (el.style.bottom = computedStyle.bottom);
          parseFloat(computedStyle.left) < 0
            ? (el.style.left = "0px")
            : (el.style.left = computedStyle.left);
          parseFloat(computedStyle.right) < 0
            ? (el.style.right = "0px")
            : (el.style.right = computedStyle.right);
          const maxAllowedWidth = computedStyle.left
            ? window.innerWidth - parseFloat(computedStyle.left)
            : window.innerWidth;
          const maxAllowedHeight = computedStyle.top
            ? window.innerHeight - parseFloat(computedStyle.top)
            : window.innerHeight;

          // Min Width
          if (
            computedStyle.minWidth &&
            computedStyle.minWidth !== "none" &&
            computedStyle.minWidth !== "auto" &&
            computedStyle.minWidth !== "0px" &&
            computedStyle.minWidth.trim() !== ""
          ) {
            this.minWidth = parseFloat(computedStyle.minWidth);
          } else {
            this.minWidth = 70; // fallback deÄŸeri
          }

          // Max Width
          if (
            computedStyle.maxWidth &&
            computedStyle.maxWidth !== "none" &&
            computedStyle.maxWidth !== "auto" &&
            computedStyle.maxWidth !== "0px" &&
            computedStyle.maxWidth.trim() !== ""
          ) {
            this.maxWidth = parseFloat(computedStyle.maxWidth);
            if (this.maxWidth > maxAllowedWidth) {
              this.maxWidth = maxAllowedWidth;
            }
          } else {
            this.maxWidth = maxAllowedWidth;
          }

          // Min Height
          if (
            computedStyle.minHeight &&
            computedStyle.minHeight !== "none" &&
            computedStyle.minHeight !== "auto" &&
            computedStyle.minHeight !== "0px" &&
            computedStyle.minHeight.trim() !== ""
          ) {
            this.minHeight = parseFloat(computedStyle.minHeight);
          } else {
            this.minHeight = 70;
          }

          // Max Height
          if (
            computedStyle.maxHeight &&
            computedStyle.maxHeight !== "none" &&
            computedStyle.maxHeight !== "auto" &&
            computedStyle.maxHeight !== "0px" &&
            computedStyle.maxHeight.trim() !== ""
          ) {
            this.maxHeight = parseFloat(computedStyle.maxHeight);
            if (this.maxHeight > maxAllowedHeight) {
              this.maxHeight = maxAllowedHeight;
            }
          } else {
            this.maxHeight = maxAllowedHeight;
          }
          const givenHeight = parseFloat(getComputedStyle(el).height);
          if (givenHeight > this.maxHeight) {
            el.style.setProperty("height", `${this.maxHeight}px`, "important");
            const limitedHeight = parseFloat(getComputedStyle(el).height);
            this.limitedTop = window.innerHeight - limitedHeight + "px";
            el.style.setProperty("top", `${this.limitedTop}px`, "important");
          }

          const givenWidth = parseFloat(getComputedStyle(el).width);
          if (givenWidth > this.maxWidth) {
            el.style.setProperty("width", `${this.maxWidth}px`, "important");
            const limitedWidth = parseFloat(getComputedStyle(el).width);
            const height = parseFloat(getComputedStyle(el).height);
            this.limitedLeft = window.innerWidth - limitedWidth + "px";
            el.style.setProperty("left", `${this.limitedLeft}px`, "important");
          }

          this.observeExternalResize();
          this.updateCalculatedSize();
          this.setDraggableBodyHeight();
          document.onclick = this.clickEvent;
          if (el) {
            this.element = el;
          } else {
            this.element = this.$refs["draggableDiv"] as HTMLDivElement;
          }
          this.$emit("mounted");
        });
      });
    },
    dragMouseDownHandler(e: MouseEvent) {
      if (!this.internalPinned && !this._renderingProps.isEditMode) {
        this.dragMouseDown(e); // âœ… sadece ÅŸartlar saÄŸlanÄ±nca Ã§aÄŸÄ±r
      } else {
        return; // âœ… aksi halde hiÃ§bir ÅŸey yapma
      }
    },
    setDraggableBodyHeight() {
      requestAnimationFrame(() => {
        const header = this.$refs["draggableDivHeader"] as HTMLDivElement;
        const body = this.$refs["draggableDivBody"] as HTMLDivElement;
        const full = this.$refs["draggableDiv"] as HTMLDivElement;
        if (header && body && full) {
          const headerHeight = header.getClientRects()[0]?.height || 0;
          const fullHeight = full.offsetHeight;
          const bodyHeight = fullHeight - headerHeight;
          body.style.setProperty(
            "height",
            bodyHeight > 0 ? `${bodyHeight}px` : "auto",
            "important"
          );
        }
        this.fixZIndex();
      });
    },
    updateCalculatedSize() {
      const el = this.$refs["draggableDiv"] as HTMLDivElement;
      const computedStyle = window.getComputedStyle(el);
      this.calculatedWidth = el.offsetWidth + "px";
      this.calculatedHeight = el.offsetHeight + "px";
      this.calculateTop = this.limitedTop
        ? this.limitedTop
        : computedStyle.top
        ? computedStyle.top
        : window.innerHeight -
          Number(this.calculatedHeight.replace(/[^0-9.]+/, "")) +
          "px";
      this.calculateLeft = this.limitedLeft
        ? this.limitedLeft
        : computedStyle.left
        ? computedStyle.left
        : 0 + "px";
      this.applyStyleWithImportant(
        this.calculatedHeight,
        this.calculatedWidth,
        el.style.position,
        this.calculateTop,
        this.calculateLeft,
        computedStyle.bottom ? computedStyle.bottom : null,
        computedStyle.right ? computedStyle.right : null
      );
    },
    observeExternalResize() {
      const el = this.$refs["draggableDiv"] as HTMLDivElement;
      this.resizeObserver = new ResizeObserver(() => {
        const newHeight = el.offsetHeight + "px";
        const newWidth = el.offsetWidth + "px";
        if (
          newHeight !== this.calculatedHeight ||
          newWidth !== this.calculatedWidth
        ) {
          this.calculatedHeight = newHeight;
          this.calculatedWidth = newWidth;
        }
      });
      this.resizeObserver.observe(el);
    },
    applyStyleWithImportant(
      height?: string | null,
      width?: string | null,
      position?: string | null,
      top?: string | null,
      left?: string | null,
      bottom?: string | null,
      right?: string | null
    ) {
      const setOrRemove = (prop: string, value?: string | null) => {
        if (value === undefined) {
          // âŒ undefined => hiÃ§ dokunma (Ã¶nceki deÄŸeri koru)
          return;
        }
        if (value === null || value === "") {
          // ðŸ”µ null veya boÅŸ string => property'i sil
          this.element.style.removeProperty(prop);
        } else {
          // âœ… string bir deÄŸer => uygula
          this.element.style.setProperty(prop, value, "important");
        }
      };

      // âœ… Ana stil Ã¶zellikleri
      setOrRemove("height", height);
      setOrRemove("width", width);
      setOrRemove("position", position);
      setOrRemove("top", top);
      setOrRemove("left", left);
      setOrRemove("bottom", bottom);
      setOrRemove("right", right);

      // ðŸ”µ FULLSCREEN & MINIMIZE durumunda limitleri kaldÄ±r
      if (this.isFullScreen || this.isMinimized) {
        this.element.style.setProperty("max-width", "none", "important");
        this.element.style.setProperty("max-height", "none", "important");
        this.element.style.setProperty("min-width", "0", "important");
        this.element.style.setProperty("min-height", "0", "important");
      } else {
        // âœ… Burada artÄ±k undefined ihtimali yok â†’ direkt uygula
        this.element.style.setProperty(
          "max-width",
          `${this.maxWidth}px`,
          "important"
        );
        this.element.style.setProperty(
          "max-height",
          `${this.maxHeight}px`,
          "important"
        );
        this.element.style.setProperty(
          "min-width",
          `${this.minWidth}px`,
          "important"
        );
        this.element.style.setProperty(
          "min-height",
          `${this.minHeight}px`,
          "important"
        );
      }

      // âœ… her style update sonunda z-index yeniden fixle
      this.fixZIndex();
    },
    closeDragElement() {
      document.onmouseup = null;
      document.onmousemove = null;
      document.ontouchend = null;
      document.ontouchmove = null;
    },
    elementDrag(mouseEvent: MouseEvent) {
      if (this.resizing) {
        return;
      }
      const currEvent: any = mouseEvent || window.event;
      currEvent.preventDefault();

      this.pos1 = this.pos3 - currEvent.clientX;
      this.pos2 = this.pos4 - currEvent.clientY;
      this.pos3 = currEvent.clientX;
      this.pos4 = currEvent.clientY;

      // Yeni pozisyon hesapla
      const newLeft = this.element.offsetLeft - this.pos1;
      const newTop = this.element.offsetTop - this.pos2;

      const maxLeft = window.innerWidth - this.element.offsetWidth - 10;
      const maxTop = window.innerHeight - this.element.offsetHeight;

      // SÄ±nÄ±rlarÄ± uygula (taÅŸmayÄ± engelle)
      const clampedLeft = Math.min(Math.max(newLeft, 0), maxLeft);
      const clampedTop = Math.min(Math.max(newTop, 0), maxTop);

      this.applyStyleWithImportant(
        this.element.style.height,
        this.element.style.width,
        this.element.style.position,
        `${clampedTop}px`,
        `${clampedLeft}px`
      );
    },
    elementMobileDrag(mouseEvent: TouchEvent) {
      const currEvent: any = mouseEvent || window.event;
      currEvent.preventDefault();
      this.pos1 = this.pos3 - currEvent.touches[0].clientX;
      this.pos2 = this.pos4 - currEvent.touches[0].clientY;
      this.pos3 = currEvent.touches[0].clientX;
      this.pos4 = currEvent.touches[0].clientY;
      this.element.style.top = this.element.offsetTop - this.pos2 + "px";
      this.element.style.left = this.element.offsetLeft - this.pos1 + "px";
    },
    dragMouseDown(mouseEvent: MouseEvent) {
      if (this.resizing) return; // Prevent dragging while resizing
      const currEvent: any = mouseEvent || window.event;
      currEvent.preventDefault();
      this.pos3 = currEvent.clientX;
      this.pos4 = currEvent.clientY;

      this.rect = this.element.getBoundingClientRect();
      this._left = 0;
      this._top = 0;
      this._bottom = window.innerHeight;
      this._right = window.innerWidth;

      document.onmouseup = this.closeDragElement;
      document.onmousemove = this.elementDrag;
      document.ontouchmove = this.elementMobileDrag;
      document.ontouchend = this.closeDragElement;

      (<any>document.activeElement).blur(); //trigger blur of inner components

      this.fixZIndex();
    },
    dragElement(elmnt: HTMLDivElement) {
      (this.pos1 = 0), (this.pos2 = 0), (this.pos3 = 0), (this.pos4 = 0);
      this.element = elmnt;
    },
    closePopup() {
      this.$emit("closePopup");
    },
    toggleFullscreen() {
      this.$nextTick(() => {
        this.isFullScreen = !this.isFullScreen;
        const el = this.$refs["draggableDiv"] as HTMLDivElement;
        this.element = el;
        let frame = document.getElementById("app") as HTMLDivElement;
        if ((<any>this).isFullScreen) {
          if (!this.preFullScreenStyle) {
            const preWidth = this.isMinimized
              ? this.currentWidth
              : this.calculatedWidth;
            const preHeight = this.isMinimized
              ? this.currentHeight
              : this.calculatedHeight;
            const preTop =
              window.innerHeight -
              Number(preHeight.replace(/[^0-9.]+/g, "")) +
              "px";
            const preLeft = frame.offsetWidth - parseFloat(preWidth) + "px";

            this.preFullScreenStyle = {
              width: preWidth || window.getComputedStyle(this.element).width,
              height: preHeight || window.getComputedStyle(this.element).height,
              position:
                window.getComputedStyle(this.element).position || "absolute",
              top: preTop,
              left: preLeft,
            };
          }
          if ((<any>this).isMinimized) {
            this.isMinimized = false;
          }
          let position = "absolute";
          (this.$refs["draggableDivBody"] as HTMLDivElement).style.setProperty(
            "display",
            "block"
          );
          let top = "0px";
          let bottom = "0px";
          let right = "0px";
          let width = frame.offsetWidth + "px";
          let height = frame.offsetHeight + "px";
          let left = this.isMinimized
            ? frame.offsetWidth - parseFloat(this.width) + "px"
            : "1px";
          this.applyStyleWithImportant(
            height,
            width,
            position,
            top,
            left,
            bottom,
            right
          );
          this.internalPinned = true;
        } else {
          if (this.preFullScreenStyle) {
            this.applyStyleWithImportant(
              this.preFullScreenStyle.height,
              this.preFullScreenStyle.width,
              this.preFullScreenStyle.position,
              this.preFullScreenStyle.top,
              this.preFullScreenStyle.left
            );
          }
          this.preFullScreenStyle = null;
          if (this.pinned) {
            this.internalPinned = this.pinned;
          } else {
            this.internalPinned = false;
          }
        }
        this.setDraggableBodyHeight();
        this.fixZIndex();
        this.$emit("toggleFullscreen", this.isFullScreen);
      });
    },
    minimizeWindow() {
      this.isMinimized = !this.isMinimized;
      let frame = document.getElementById("app") as HTMLDivElement;
      if ((<any>this).isMinimized) {
        this.isFullScreen = false;
        this.internalPinned = false;
        this.internalResizable = false;
        const gapBetweenTabs = 10;
        this.currentHeight = this.calculatedHeight;
        this.currentWidth = this.calculatedWidth;

        let height = "50px";
        let width = "150px";
        let position = "fixed";
        let top = `${window.innerHeight - 50}px`;
        let left = "auto";
        let right = this.insetRight
          ? `calc(${this.insetRight}px)`
          : `${
              document.querySelectorAll(".minimized").length *
              (150 + gapBetweenTabs)
            }px`;
        let bottom = "auto";
        this.applyStyleWithImportant(
          height,
          width,
          position,
          top,
          left,
          bottom,
          right
        );
        (
          this.element.children[
            this.element.children.length - 1
          ] as HTMLDivElement
        ).style.setProperty("display", "none");
        (this.element.children[0] as HTMLDivElement).style.setProperty(
          "box-shadow",
          "none"
        );
        (this.element.children[0] as HTMLDivElement).style.setProperty(
          "box-sizing",
          "content-box"
        );
        this.insetRight = this.element.style.right;
      } else {
        this.internalResizable = this.resizable ?? false;
        this.internalPinned = this.pinned ?? false;
        let position = this.preFullScreenStyle?.position || "absolute";
        let top =
          this.preFullScreenStyle?.top ||
          window.innerHeight -
            Number(this.currentHeight.replace(/[^0-9.]+/, "")) +
            "px";
        let width = this.preFullScreenStyle?.width || this.currentWidth;
        let height = this.preFullScreenStyle?.height || this.currentHeight;
        let left =
          this.preFullScreenStyle?.left ||
          frame.offsetWidth - parseFloat(this.currentWidth) + "px";
        this.applyStyleWithImportant(height, width, position, top, left);
        (
          this.element.children[
            this.element.children.length - 1
          ] as HTMLDivElement
        ).style.setProperty("display", "block");
        this.setDraggableBodyHeight();
      }
      this.fixZIndex();
      this.$emit("minimizePopup", this.isMinimized);
    },
    resizeMouseDown(
      event: MouseEvent,
      handle: "top-left" | "top-right" | "bottom-left" | "bottom-right"
    ) {
      if ((<any>this).isFullScreen) return;
      this.resizing = true;
      this.resizeHandle = handle;
      this.initialRect = this.element.getBoundingClientRect();
      document.addEventListener("mousemove", this.resizeMouseMove);
      document.addEventListener("mouseup", this.resizeMouseUp);
    },
    resizeMouseMove(event) {
      let frame = document.getElementById("app") as HTMLDivElement;
      if (!this.resizing || this.initialRect === null) return;

      const offsetX = event.clientX - this.initialRect.left;
      const offsetY = event.clientY - this.initialRect.top;
      const offsetRightX =
        event.clientX - (this.initialRect.left + this.initialRect.width);
      const offsetRightY =
        event.clientY - (this.initialRect.top + this.initialRect.height);

      const visibleAreaWidth = frame.offsetWidth;
      const visibleAreaHeight = frame.offsetHeight;

      const el = this.$refs["draggableDiv"] as HTMLDivElement;
      const computedStyle = window.getComputedStyle(el);

      const minWidth = this.minWidth ?? 70;
      const minHeight = this.minHeight ?? 70;

      const parsedMaxHeight = parseFloat(computedStyle.maxHeight);
      const validMaxHeight =
        !isNaN(parsedMaxHeight) && parsedMaxHeight > minHeight;
      const parsedMaxWidth = parseFloat(computedStyle.maxWidth);
      const validMaxWidth = !isNaN(parsedMaxWidth) && parsedMaxWidth > minWidth;

      this.maxWidth = validMaxWidth ? parsedMaxWidth : window.innerWidth;
      this.maxHeight = validMaxHeight ? parsedMaxHeight : window.innerHeight;
      const maxWidth = this.maxWidth;
      const maxHeight = this.maxHeight;

      let newWidth = this.initialRect.width;
      let newHeight = this.initialRect.height;
      let newLeft = this.initialRect.left;
      let newTop = this.initialRect.top;

      switch (this.resizeHandle) {
        case "top-left":
          newWidth = this.initialRect.width - offsetX;
          newHeight = this.initialRect.height - offsetY;
          newLeft = this.initialRect.left + offsetX;
          newTop = this.initialRect.top + offsetY;
          break;
        case "bottom-left":
          newWidth = this.initialRect.width - offsetX;
          newHeight = this.initialRect.height + offsetRightY;
          newLeft = this.initialRect.left + offsetX;
          break;
        case "top-right":
          newWidth = this.initialRect.width + offsetRightX;
          newHeight = this.initialRect.height - offsetY;
          newTop = this.initialRect.top + offsetY;
          break;
        case "bottom-right":
          newWidth = this.initialRect.width + offsetRightX;
          newHeight = this.initialRect.height + offsetRightY;
          break;
      }

      // âœ… MIN/MAX kontrol
      const exceedsMaxWidth = newWidth >= maxWidth;
      const exceedsMaxHeight = newHeight >= maxHeight;
      this.reachedMaxSize = exceedsMaxWidth || exceedsMaxHeight;

      const belowMinWidth = newWidth <= minWidth;
      const belowMinHeight = newHeight <= minHeight;
      this.reachedMinSize = belowMinWidth || belowMinHeight;
      // Yeni konumdan taÅŸma olacak mÄ± kontrol et
      const exceedsRightEdge =
        this.resizeHandle.includes("right") &&
        newLeft + newWidth >= visibleAreaWidth;
      const exceedsBottomEdge =
        this.resizeHandle.includes("bottom") &&
        newTop + newHeight >= visibleAreaHeight;
      const exceedsLeftEdge =
        this.resizeHandle.includes("left") && newLeft <= 0;
      const exceedsTopEdge = this.resizeHandle.includes("top") && newTop <= 0;

      const hitsViewportEdge =
        exceedsRightEdge ||
        exceedsBottomEdge ||
        exceedsLeftEdge ||
        exceedsTopEdge;

      if (hitsViewportEdge) {
        this.reachedMaxSize = true;
        return; // âŒ TaÅŸmasÄ±n â†’ iÅŸlem yapma
      }

      // âœ… WIDTH & HEIGHT uygula
      if (!this.reachedMinSize && !this.reachedMaxSize) {
        newWidth = Math.max(newWidth, minWidth);
        newHeight = Math.max(newHeight, minHeight);

        this.calculatedWidth = `${newWidth}px`;
        this.calculatedHeight = `${newHeight}px`;

        this.applyStyleWithImportant(`${newHeight}px`, `${newWidth}px`);

        this.setDraggableBodyHeight();
      }

      // âœ… LEFT uygula (sadece top-left & bottom-left senaryolarÄ±nda)
      if (
        (this.resizeHandle === "top-left" ||
          this.resizeHandle === "bottom-left") &&
        newLeft >= 0 &&
        newLeft + newWidth <= visibleAreaWidth &&
        !this.reachedMinSize &&
        !this.reachedMaxSize
      ) {
        this.applyStyleWithImportant(
          undefined,
          undefined,
          undefined,
          undefined,
          `${newLeft}px`
        );
        this.calculateLeft = `${newLeft}px`;
      }

      // âœ… TOP uygula (sadece top-left & top-right senaryolarÄ±nda)
      if (
        (this.resizeHandle === "top-left" ||
          this.resizeHandle === "top-right") &&
        newTop >= 0 &&
        newTop + newHeight <= visibleAreaHeight &&
        !this.reachedMinSize &&
        !this.reachedMaxSize
      ) {
        this.applyStyleWithImportant(
          undefined,
          undefined,
          undefined,
          `${newTop}px`
        );
        this.calculateTop = `${newTop}px`;
      }
    },
    resizeMouseUp() {
      this.resizing = false;
      this.reachedMinSize = false;
      this.reachedMaxSize = false;
      this.resizeHandle = "";
      this.initialRect = null;
      document.removeEventListener("mousemove", this.resizeMouseMove);
      document.removeEventListener("mouseup", this.resizeMouseUp);
      this.setDraggableBodyHeight();
    },
    clickEvent(e: Event) {
      // var documentObject = document.getElementById("draggableDiv");
      // if (
      //   documentObject &&
      //   e.target instanceof HTMLElement &&
      //   e.target &&
      //   documentObject.contains(e.target)
      // ) {
      //   this.$emit("clickInside");
      // } else {
      //   this.$emit("clickOutside");
      // }
      const el = this.$refs["draggableDiv"] as HTMLDivElement;
      if (!el) return;

      // âœ… Her tÃ¼rlÃ¼ DOM node kabul edilir (HTMLElement, SVGElement, Text vs.)
      if (e.target instanceof Node && el.contains(e.target)) {
        this.$emit("clickInside");
      } else {
        this.$emit("clickOutside");
      }
    },
    fixZIndex() {
      if (
        this.zIndex === null ||
        this.zIndex === undefined ||
        this.zIndex === ""
      ) {
        document.getElementsByName("draggableDiv").forEach((element) => {
          element.style.setProperty("z-index", "9", "important");
        });
        this.element.style.setProperty("z-index", "12", "important");
      } else {
        document.getElementsByName("draggableDiv").forEach((element) => {
          //zIndex must be an integer (proptan zIndex number verilmeli)
          let strzIndex = <any>this.zIndex - 1;
          element.style.setProperty(
            "z-index",
            strzIndex.toString(),
            "important"
          );
        });
        this.element.style.setProperty("z-index", this.zIndex, "important");
      }
    },
    configureForEditor() {
      if (
        this._renderingProps.isEditMode ||
        this._renderingProps.isEditMode == null ||
        this._renderingProps.isEditMode == undefined
      )
        (<HTMLDivElement>this.$refs["draggableDiv"])!.style.setProperty(
          "position",
          "relative"
        );
      else
        (<HTMLDivElement>this.$refs["draggableDiv"])!.style.setProperty(
          "position",
          "absolute"
        );
    },
  },
  watch: {
    pinned(val) {
      this.internalPinned = val;
    },
    resizable(val) {
      this.internalResizable = val;
    },
    isVisible(val) {
      if (val) {
        this.$nextTick(() => this.doInitialMeasurements());
      }
    },
  },
  beforeDestroy() {
    this.resizeObserver?.disconnect();
    document.removeEventListener("click", this.clickEvent);
  },
});
